import React from 'react';
import styles from './footer.module.css'; // For custom styles
import logo from "../../assets/general/logo.svg"
import { Link } from 'react-router-dom';
const Footer = () => {
  return (
    <footer className={`${styles.footer}`}>
      <div className="container">
        <div className="row">
          <div className="col-lg-3 col-md-6">
            <div className={styles.firstItem}>
              <div className={styles.logoContainer}>
                <img className={styles.logo}src={logo} alt="logo" />
                <p>Local Cafe</p>
              </div>
              <ul className="list-unstyled">

                <li>24 Milton Rd E, Edinburgh EH15 2PQ</li>
                <li>schools@edinburghcollege.ac.uk</li>
                <li>0131 669 4400</li>
              </ul>
            </div>
          </div>

          <div className="col-lg-3 col-md-6">
            <h4>Shopping &amp; Categories</h4>
            <ul className="list-unstyled">

              <li> <Link to="/" >Home</Link></li>
              <li> <Link to="/products" >Products</Link></li>
              <li> <Link to="/contact" >Contact</Link></li>
            </ul>
          </div>

          <div className="col-lg-3 col-md-6">
            <h4>Useful Links</h4>
            <ul className="list-unstyled">
            <li> <Link to="/team" >Team</Link></li>
            <li> <Link to="/about" >About</Link></li>
            <li> <Link to="/" >Home</Link></li>
            </ul>
          </div>

          <div className="col-lg-3 col-md-6">
            <h4>Help &amp; Products</h4>
            <ul className="list-unstyled">
              <li> <Link to="/products/muffins" >muffins</Link></li>
              <li> <Link to="/products/vegan" >vegan</Link></li>
              <li> <Link to="/products/cupcakes" >cupcakes</Link></li>
            </ul>
          </div>

          <div className="col-lg-12">
            <div className={styles.underFooter}>
              <ul className="d-flex justify-content-center gap-3 list-unstyled">
                <li><a href="#"><i className="fa-brands fa-facebook-f"></i></a></li>
                <li><a href="#"><i className="fa-brands fa-square-twitter"></i></a></li>
                <li><a href="#"><i className="fa-brands fa-linkedin"></i></a></li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
