import React, { useState } from 'react';
import Dishcart from "../../components/dish_cart/dish_cart";
import styles from './vegans.module.css';
import dishes from "../../data/data";
import ProductsGrid from "../../components/products_grid/productsGrid";
import { Link } from 'react-router-dom';
import Wrapper from '../../components/wrapper/wrapper';
const Muffins = ({onBodyMask}) => {
  const [selectedCartIndex, setSelectedCartIndex] = useState(null);
  const handleCartClick = (index) => {

    if (selectedCartIndex === index) {
      onBodyMask(false)
      setSelectedCartIndex(null);
    } else {
      onBodyMask(true)
      setSelectedCartIndex(index);
    }
  };

  const muffinsItems = Object.values(dishes.veganDishes);

  return (
    <Wrapper>


    <div className={styles.container}>
     <h1 className={styles.heading}>
      <Link to="/products" className={styles.navLink}>Products {'>'}</Link> Muffins
      </h1>
        <ProductsGrid>
        {muffinsItems.map((item, index) => (
          <Dishcart
            key={index}
            item={item}
            isSelected={index === selectedCartIndex}
            onClick={() => handleCartClick(index)}
          />
        ))}
         </ProductsGrid>
    </div>
    </Wrapper>
  );
};

export default Muffins;