

import React from 'react';
import styles from './member_cart.module.css'

const Membercart = ({member}) => {
  return (

    <div className={styles.teamMemberCart} >
    <div className={styles.teamImgContainer}>
      <img src={member.img} alt={member.name} className="team-member-img" />
    </div>
    <div className={styles.teamDescContainer}>
      <h3>{member.name}</h3>
      <p>{member.role}</p>
      <p>{member.description}</p>
    </div>

  </div>

  );
};

export default Membercart;
