// src/App.js
import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Layout from './components/layout/layout';
import Home from './pages/home/home';
import About from './pages/about/about';

import Products from './pages/products/products';

import Cupcakes from './pages/cupcakes/cupcakes';
import Muffins from './pages/muffins/muffins';
import Vegans from './pages/vegans/vegans';
import Team from './pages/team/team';
import { useState } from 'react';

const App = () => {
  const [bodyMask, setbodyMask] = useState(false);
  return (
    <Router>
      <Layout bodyMask={bodyMask}>
        <Routes>
          <Route path="/" element={<Home /> } />
          <Route path="/about" element={<About />} />
          <Route path="/products" element={<Products />} />
          <Route path="/team" element={<Team />} />
          <Route path="/products/muffins" element={<Muffins onBodyMask={setbodyMask}/>} />
          <Route path="/products/cupcakes" element={<Cupcakes onBodyMask={setbodyMask}/>} />
          <Route path="/products/vegan" element={<Vegans onBodyMask={setbodyMask}/>} />
        </Routes>
      </Layout>

    </Router>
  );
};

export default App;
