import React, { useState } from 'react';


import MembersGrid from "../../components/member_grid/membersGrid";
import ProductsGrid from "../../components/products_grid/productsGrid";
import Membercart from "../../components/memeber_cart/member_cart";
import styles from './team.module.css';
import Wrapper from '../../components/wrapper/wrapper';
import teamMembers from "../../data/data_team";

const Team = () => {
  const membersArray = Object.values(teamMembers);

  return (
    <Wrapper>


    <div className={styles.container}>
      <h1 className={styles.heading}>Our Team</h1>
      <MembersGrid>

        {membersArray.map((member, index) => (
          <Membercart key={index} member={member}/>
        ))}
      </MembersGrid>
    </div>
    </Wrapper>
  );
};

export default Team;
