import React from 'react';
import styles from "./about.module.css"
import Wrapper from "../../components/wrapper/wrapper"
const About = () => {
  return (
    <Wrapper>


      <div className={styles.container}>
        <h2 className={styles.header}>About Our Cafe</h2>
        <p className={styles.paragraph}>
          Welcome to <strong>Our Local Cafe</strong>, where we serve the finest coffee,
          delicious pastries, and a warm atmosphere for all our guests. Whether you're
          stopping by for your morning coffee, having a casual meet-up, or simply looking
          for a relaxing spot to unwind, weve got something special for you!
        </p>

        <h3 className={styles.subheader}>Our Story</h3>
        <p className={styles.paragraph}>
          Our journey started in 2010 with a passion for high-quality coffee and a desire
          to create a space where people could gather and share great conversations. Over the
          years, we've grown from a small corner shop to a beloved neighborhood cafe, with a
          loyal customer base and a team that loves to make your experience unforgettable.
        </p>

        <h3 className={styles.subheader}>What We Offer</h3>
        <ul className={styles.list}>
          <li> Freshly baked pastries made daily</li>
          <li> Locally roasted coffee beans brewed to perfection</li>
          <li> Vegan and gluten-free options</li>
          <li> Cozy atmosphere with live music events on weekends</li>
          <li> A selection of books for you to enjoy while you sip</li>
        </ul>

        <h3 className={styles.subheader}>Our Values</h3>
        <p className={styles.paragraph}>
          At <strong>Our Local Cafe</strong>, we believe in sustainability, community, and
          quality. We source our ingredients locally and strive to reduce our environmental
          impact while supporting local farmers and artisans.
        </p>
      </div>
    </Wrapper>
  );
};



export default About;
