import React, { useState } from 'react';
import styles from './productsNav.module.css'
import  cupcake  from "./assets/img.jpg"
import    vegan from "./assets/img2.jpg"
import   muffin   from "./assets/img3.jpg"

const ProductsNav = ({children}) => {


  return (
    <div className={styles.container_products}>


      <h1 className={styles.heading}>{children}</h1>

      <div className={styles.navWrapper} >
        <a href="/products/cupcakes" className={`${styles.button} ${styles.button_left}`}>
          <h2 className={styles.listItem} >CUPCAKES</h2>
          <img src={cupcake}/>
        </a>
        <a href="/products/muffins" className={`${styles.button} ${styles.button_center}`}>
          <h2  className={styles.listItem}>MUFFINS</h2>
          <img src={muffin}/>
        </a>
        <a href="/products/vegan" className={`${styles.button} ${styles.button_right}`}>
          <h2  className={styles.listItem}>VEGAN</h2>
          <img src={vegan}/>
        </a>
      </div>

    </div>
  );
};


export default ProductsNav;
