import React from 'react';
import styles from './home.module.css';
import Contact from './component/contact'
import { Link } from 'react-router-dom';

import { ThemeProvider, createTheme } from '@mui/material/styles';
import { Button } from '@mui/material';
const theme = createTheme({
  palette: {
    primary: {
      main: '#499de7;', // Example primary color
    },
    secondary: {
      main: '#d32f2f', // Example secondary color
    },
  },
  typography: {
    fontFamily: '"Arial", sans-serif', // Example font family
  },
});

const Home = () => {
  return (

    <ThemeProvider theme={theme}>
      <main className={styles.container}>
        <h1 className={styles.heading}>Welcome to Our Cafe</h1>
        <p className={styles.tagline}>Super, Extra, Nice, Awesome coffee and more..</p>
        <div className={styles.buttonContainer}>
        <Link to="/products" className={styles.navLink}>
          <Button sx={{ color: '#fff' }} Button variant="contained" color="primary">
              Get Started
          </Button>
        </Link>
        <Link to="/about" className={styles.navLink}>
          <Button sx={{ color: '#fff' }} Button variant="contained" color="primary">
            Learn more
          </Button>
        </Link>


        </div>


      </main>
      <Contact/>
    </ThemeProvider>
  );
};

export default Home;
