// src/components/Header.js
import React from 'react';
import { Link } from 'react-router-dom';
import logo from "../../assets/general/logo.svg"
import styles from './header.module.css'

import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Drawer,
  List,
  ListItem,
  ListItemText,
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { useState } from 'react';

const Header = () => {
  const [open, setOpen] = useState(false);

  const toggleDrawer = (state) => () => {
    setOpen(state);
  };

  return (
    <>
      <AppBar   position="sticky"  sx={{ zIndex: 2, backgroundColor: '#499de7'}}>
        <Toolbar  sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Link to="/" className={styles.navLink}>
                  <img className={styles.logo}src={logo}/>
            </Link>
          <IconButton
            edge="start"
            color="inherit"
            onClick={toggleDrawer(true)}
            aria-label="menu"
            sx={{ mr: 2 }}
          >
          <MenuIcon className={styles.hamb_mobile} />
          </IconButton>

          <div  className={styles.container_nav}>

              <ul className={styles.navList}>

              <li className={styles.navItem}>
                <Link to="/" className={styles.navLink}>Home</Link>
                </li>
              <li className={styles.navItem}>
                <Link to="/about" className={styles.navLink}>About</Link>
              </li>

              <li className={styles.navItem}>
                <Link to="/products" className={styles.navLink}>Products</Link>
              </li>
              <li className={styles.navItem}>
                <Link to="/team" className={styles.navLink}>Team</Link>
              </li>
          </ul>
          </div>

        </Toolbar>



      </AppBar>

      <Drawer anchor="left" open={open} onClose={toggleDrawer(false)}>
        <List sx={{ width: 250 }}>
          <ListItem button onClick={() => setOpen(false)}>
            <Link to="/" className={styles.navLinkHamb}>Home</Link>
          </ListItem>
          <ListItem button onClick={() => setOpen(false)}>
            <Link to="/about" className={styles.navLinkHamb}>About</Link>
          </ListItem>
          <ListItem button onClick={() => setOpen(false)}>
            <Link Link to="/products" className={styles.navLinkHamb}>Products</Link>
          </ListItem>
          <ListItem button onClick={() => setOpen(false)}>
            <Link to="/team" className={styles.navLinkHamb}>Team</Link>
          </ListItem>
        </List>
      </Drawer>
    </>
  );
};


export default Header;
