import React from 'react';
import styles from './dish_cart.module.css';

const Dishcart = ({ item, isSelected, onClick }) => {
  return (
    <div
      className={`${styles.dish_cart} ${isSelected ? styles.active_cart : styles.inactive_cart}`}
      onClick={onClick}
    >
      <div className={styles.dish_cart_info}>
        <h2>{item.name}</h2>
        <b><p>Price: {item.price}</p></b>
        <span className={`${styles.description_cart} ${isSelected ? styles.active_description : styles.inactive_description}`}>
          {item.description}
        </span>
      </div>
      <div className={`${styles.dish_cart_img_container}  ${isSelected ? styles.active_dish_cart_img_container : ''}`}>
        <img src={item.img} alt={item.name} />
      </div>
    </div>
  );
};

export default Dishcart;
